<template>
  <div class="option-list-main-page" ref="UI">
<!--    <div id="option-list-base-btn" @click="showMenu = !showMenu">-->
    <div id="option-list-base-btn" @click="showMenuClick">
      <slot><span class="base-btn">···</span></slot>
    </div>
<!--      <ul class="option-list" v-on:mouseenter="visible" @mouseleave="invisible" @mouseover="mouseover($event)">
        <li inde="1">
          <img src="../assets/icons/复制微信链接@2x.png" alt="" v-show="activeIndex !== '1'">
          <img src="../assets/icons/复制微信链接-蓝@2x.png" alt="" v-show="activeIndex === '1'">
          复制微信链接</li>
        <li inde="2">复制网银链接</li>
        <li inde="3">预览</li>
        <li inde="4">编辑</li>
        <li inde="5">删除</li>
        <li inde="6">
          下载中奖名单
          <ul v-show="activeIndex && activeIndex.indexOf('6') > -1" style="position: absolute; left: 100%; bottom: 0; transform: translate(90px, 200%);">
            <li inde="6.1">选项一</li>
            <li inde="6.2">选项二</li>
            <li inde="6.3">选项三</li>
          </ul>
        </li>
      </ul>-->
      <ul class="option-list-base" :class="{'option-list': showMenu, 'hidden': !showMenu}" @mouseover="mouseover($event)" @mouseleave="invisible">
        <li v-for="item in options" :inde="item.value" @click="itemClick(item)">
          <img :src="item.baseIcon" alt="" v-show="item.baseIcon && !showActIcon(item)">
          <img :src="item.activeIcon" alt="" v-show="item.activeIcon && showActIcon(item)">
          {{ item.label }}
          <ul class="option-list-second" v-if="item.children" @mouseover="mouseover($event)" v-show="activeIndex && activeIndex.split('.')[0] === item.value">
            <!--        <ul class="option-list-second" v-if="item.children" @mouseover="mouseover($event)" v-show="true">-->
            <li v-for="citem in item.children" :inde="citem.value" @click="itemClick(citem)">
              <img :src="citem.baseIcon" alt="" v-show="citem.baseIcon && activeIndex !== citem.value">
<!--              <img :src="citem.baseIcon" alt="" v-show="true">-->
              <img :src="citem.activeIcon" alt="" v-show="citem.activeIcon && activeIndex === citem.value">
              {{ citem.label }}
            </li>
          </ul>
        </li>
      </ul>
  </div>
</template>

<script>
  export default {
    name: "OptionList",
    props: {
      popper_position: {
        type: Object,
        default() {
          return {
            width: '100px',
            positionX: '-75px'
          };
        }
      },
      options: {
        type: Array,
        default() {
          return [
            {
              value: '1',
              label: '查看',
              baseIcon: require('assets/touchContent/seedstore/eye@3x.png'),
              activeIcon: require('assets/touchContent/seedstore/eye_active.png'),
            }, {
              value: '2',
              label: '编辑',
              baseIcon: require('assets/touchContent/seedstore/编辑@2x.png'),
              activeIcon: require('assets/touchContent/seedstore/编辑-蓝@2x.png'),
            }, {
              value: '3',
              label: '删除',
              baseIcon: require('assets/touchContent/seedstore/删除@2x.png'),
              activeIcon: require('assets/touchContent/seedstore/删除-蓝@2x.png'),
            },
          ]
        }
      }
    },
    data() {
      return {
        // showCode: false,
        activeIndex: null,
        showMenu: false,
        baseOptions: [
          {
            value: '1',
            label: '复制网银链接',
            baseIcon: require('../assets/icons/复制微信链接@2x.png'),
            activeIcon: require('../assets/icons/复制微信链接-蓝@2x.png'),
            children: [
              {
                value: '1.1',
                label: '选项1.1',
                baseIcon: require('../assets/icons/复制微信链接@2x.png'),
                activeIcon: require('../assets/icons/复制微信链接-蓝@2x.png'),
              },
              {
                value: '1.2',
                label: '选项1.2',
              },
              {
                value: '1.3',
                label: '选项1.3',
              }
            ]
          },
          {
            value: '2',
            label: '复制微信链接',
            baseIcon: require('../assets/icons/复制微信链接@2x.png'),
            activeIcon: require('../assets/icons/复制微信链接-蓝@2x.png'),
          },          {
            value: '3',
            label: '预览',
          },          {
            value: '4',
            label: '编辑',
            children: [
              {
                value: '4.1',
                label: '选项4.1',
              },
              {
                value: '4.2',
                label: '选项4.2',
              },
              {
                value: '4.3',
                label: '选项4.3',
              }
            ]
          },          {
            value: '5',
            label: '删除',
          },          {
            value: '6',
            label: '下载中奖名单',
            children: [
              {
                value: '6.1',
                label: '选项6.1',
              },
              {
                value: '6.2',
                label: '选项6.2',
              },
              {
                value: '6.3',
                label: '选项6.3',
              }
            ]
          },
        ]
      }
    },
    mounted() {
      this.$refs.UI.style.setProperty('--options-position-x', this.popper_position.positionX || '-75x' );
      this.$refs.UI.style.setProperty('--options-width', this.popper_position.width || '100px' );
      window.addEventListener('click', this.lisClickOutside)
      // let optionlist = document.querySelector('.option-list').childNodes
      // console.log(optionlist);
      // console.log(document.activeElement, '--active el');
    },
    computed: {
      showActIcon() {
        return function(item) {
         return this.activeIndex && (this.activeIndex === item.value || this.activeIndex.split('.')[0] === item.value)
        }
      },
    },
    methods: {
      /**
       * 监听是否在列表外发生的点击
      * */
      lisClickOutside(e) {
        let x = e.path.some(function (item) {
          return item.id === "option-list-base-btn";
        })
        if(!x) this.showMenu = false;
      },
      // visible:function(){
      //   this.showCode = true;
      // },
      invisible:function(){
        this.activeIndex = null;
      },
      mouseover(e) {
        if (e.path[0].localName !== 'li') return
        this.activeIndex = ( e.path[0].attributes.inde && e.path[0].attributes.inde.value ) || null;
      },
      itemClick(item) {
        event.stopPropagation();

        // console.log(item, '--in OptionList.vue');
        this.$bus.$emit('OptionList_itemClick', item)
      },
      showMenuClick() {
        window.event? window.event.cancelBubble = true : e.stopPropagation();
        this.showMenu = !this.showMenu
      }
    }
  }
</script>

